.bi-layout-canvas {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 1;
}
.bi-layout-canvas-track {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  overflow-x: hidden;
  overflow-y: auto;
}
.bi-layout-element {
  user-select: none;
  position: absolute;
  background: #fff;
  border: 1px solid transparent;
}
.bi-layout-element:not(.__active) {
  transition: all ease 0.3s;
}
.bi-layout-element.__active {
  border-color: #a855f7;
  z-index: 90;
}
.bi-layout-element-children {
  position: relative;
  height: 100%;
}
/*                              */
/*        resize指示器           */
/*                              */
.bi-layout-resize-indicator {
  position: absolute;
  background: rgba(168, 85, 247, 0.3);
  user-select: none;
}
.bi-layout-resize-indicator.__active {
  cursor: move;
  z-index: 97;
  border: 1px solid #a855f7;
  background: rgba(168, 85, 247, 0.1);
}
/*                            */
/*        锚点 mixin           */
/*                            */
/* prettier-ignore-start */
.bi-layout-resize-anchor-nw {
  position: absolute;
  z-index: 100;
  width: 8;
  height: 8;
  background: #a855f7;
  top: -4px;
  left: -4px;
  cursor: nw-resize;
}
.bi-layout-resize-anchor-n {
  position: absolute;
  z-index: 100;
  width: 8;
  height: 8;
  background: #a855f7;
  top: -4px;
  left: 50%;
  cursor: n-resize;
  margin-left: -4px;
}
.bi-layout-resize-anchor-ne {
  position: absolute;
  z-index: 100;
  width: 8;
  height: 8;
  background: #a855f7;
  top: -4px;
  right: -4px;
  cursor: ne-resize;
}
.bi-layout-resize-anchor-e {
  position: absolute;
  z-index: 100;
  width: 8;
  height: 8;
  background: #a855f7;
  top: 50%;
  right: -4px;
  cursor: e-resize;
  margin-top: -4px;
}
.bi-layout-resize-anchor-se {
  position: absolute;
  z-index: 100;
  width: 8;
  height: 8;
  background: #a855f7;
  bottom: -4px;
  right: -4px;
  cursor: se-resize;
}
.bi-layout-resize-anchor-s {
  position: absolute;
  z-index: 100;
  width: 8;
  height: 8;
  background: #a855f7;
  bottom: -4px;
  left: 50%;
  cursor: s-resize;
  margin-left: -4px;
}
.bi-layout-resize-anchor-sw {
  position: absolute;
  z-index: 100;
  width: 8;
  height: 8;
  background: #a855f7;
  bottom: -4px;
  left: -4px;
  cursor: sw-resize;
}
.bi-layout-resize-anchor-w {
  position: absolute;
  z-index: 100;
  width: 8;
  height: 8;
  background: #a855f7;
  top: 50%;
  left: -4px;
  cursor: w-resize;
  margin-top: -4px;
}
/* prettier-ignore-end */
/* prettier-ignore-start */
